'use strict'; // Convenience functions for extracting non-subsystem-specific data from protobuf states

function getSubsystems(state) {
  if (!state) return null;
  return state.getSubsystems();
} // TODO: deprecate or rename this


function getTime(state) {
  if (!state) return null;
  return state.getInstantTs().getSeconds();
} // TODO: rename 'timepoints' to 'states' everywhere


function getLatestTimepoint(timepoints) {
  if (!timepoints.length) return null;
  return timepoints[timepoints.length - 1];
}

function getStateTimes(state) {
  if (!state) return null; // Despite the name, 'getSeconds' returns a timestamp with milisecond granularity
  // TODO: check this is still true when wired up to real data from go-libp2p

  var end = getTime(state);
  var duration = state.getSnapshotDurationMs();
  var start = end - duration;
  return {
    start: start,
    end: end,
    duration: duration
  };
}

function getTimeIndex(states, timestamp) {
  return states.findIndex(function (state) {
    return getTime(state) === timestamp;
  });
}

module.exports = {
  getLatestTimepoint: getLatestTimepoint,
  getSubsystems: getSubsystems,
  getStateTimes: getStateTimes,
  getTime: getTime,
  getTimeIndex: getTimeIndex
};